.section {
  border-style: solid;
  border-color: #868686;
  border-width: 4px 2px 4px 2px;
  width: 284px;
  min-height: 114px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #ffffff;
}

.wrapper {
  background: #ffece0;
  width: 100%;
  min-height: 31px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #d1d1d1;
}

.text {
  font-family: "Roboto";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18.75px;
  margin: 5px 10px 7px 10px;
}

.block {
  width: 100%;
  height: 50px;
}

.button {
  background: none;
  border: none;
  cursor: pointer;
}
